import React from "react";
import { ImageItem } from "./CharactarPicker";
import { createUseStyles } from "react-jss";

const useStyles = createUseStyles({
  GenerateButton: {
    marginTop: 100,
    userSelect: "none",
    fontSize: "300%",
    color: "white",
    transition: "0.2s",
    "&:hover": {
      color: "gold",
      cursor: "pointer"
    }
  }
});

const getDD2Charactars = (id: number) => {
  return `./dd2_chars/char${id}.png`;
};

export const generateAllImages = (): ImageItem[] => {
  return new Array(9).fill(0).map((_, i) => {
    return { select: false, image: getDD2Charactars(i + 1) };
  });
};

export default function ClickHereToRandomaize(props: {
  doRandom: (images: ImageItem[]) => void;
}): JSX.Element {
  const styleClasses = useStyles({});
  const Click = React.useCallback(() => {
    const images = generateAllImages();

    while (images.reduce((sum, item) => sum + Number(item.select), 0) < 4) {
      while (true) {
        const item = images[Math.floor(Math.random() * images.length)];
        if (item.select) continue;
        item.select = true;
        break;
      }
    }

    props.doRandom(images);
  }, [props]);
  return (
    <div className={styleClasses.GenerateButton} onClick={Click}>
      Generate
    </div>
  );
}
