import { createUseStyles } from "react-jss";
import React from "react";

export interface ImageItem {
  image: string;
  select: boolean;
}

const useStyles = createUseStyles({
  dd2Container: {
    marginTop: 100,
    display: "flex",
    flexDirection: "row",
    borderWidth: 0,
    borderStyle: "solid",
    borderColor: "red"
  },
  dd2ImageContainer: {
    position: "relative",
    width: "100%",
    height: "100%"
  },
  dd2ImageContainerHighlight: {
    position: "absolute",
    left: 0,
    top: 0,
    zIndex: -1,
    width: "100%",
    height: "100%",
    transform: "rotateZ(45deg) scale(calc(1/1.4142))",
    boxShadow: "0 0 13px 8px gold, inset 0 0 13px 8px gold"
  }
});

const Image = (props: ImageItem): JSX.Element => {
  const styleClasses = useStyles({});
  return (
    <div
      className={styleClasses.dd2ImageContainer}
      style={{ zIndex: props.select ? 1 : 0 }}
    >
      <div
        hidden={!props.select}
        className={styleClasses.dd2ImageContainerHighlight}
      />
      <img alt={""} width={"100%"} height={"100%"} src={props.image} />
    </div>
  );
};

export default function CharactarPicker(props: {
  images: ImageItem[];
}): JSX.Element {
  const styleClasses = useStyles({});

  const DD2Image = React.useMemo(() => {
    return props.images.map((item, key) => {
      return <Image image={item.image} key={key} select={item.select} />;
    });
  }, [props]);

  return <div className={styleClasses.dd2Container}>{DD2Image}</div>;
}
