import "./styles.css";
import CharactarPicker, { ImageItem } from "./CharactarPicker";
import React from "react";
import ClickHereToRandomaize, {
  generateAllImages
} from "./ClickHereToRandomaize";
import { createUseStyles } from "react-jss";

const useStyles = createUseStyles({
  Header: {
    textShadow: "0px 0px 20px gold",
    color: "chocolate",
    fontSize: "300%"
  },
  Credit: {
    position: "absolute",
    left: 20,
    bottom: 50,
    color: "white",
    textAlign: "left"
  }
});

function Credit(): JSX.Element {
  const styleClasses = useStyles({});
  return (
    <div className={styleClasses.Credit}>
      <span>Made by: Yoav Ram</span>
      <br />
      <span>Using: codesandbox</span>
      <br />
      <a href={"https://github.com/yoyo421"}>
        <span>https://github.com/yoyo421</span>
      </a>
    </div>
  );
}

export default function App() {
  const styleClasses = useStyles({});
  const Title = React.useMemo(
    () => "Darkest Dungeon II Character Generator",
    []
  );

  const [images, setImages] = React.useState<ImageItem[]>(generateAllImages());
  React.useEffect(() => {
    document.title = Title;
  });
  return (
    <div className="App">
      <div>
        <span className={styleClasses.Header}>{Title}</span>
      </div>
      <CharactarPicker images={images} />
      <ClickHereToRandomaize doRandom={setImages} />
      <Credit />
    </div>
  );
}
